.sendForm {
  height: 50px;
  margin: 30px auto;

  display: flex;
  gap: 20px;
  align-items: center;
}

.textarea {
  width: 100%;
  border-radius: 20px;
  padding: 12px;
  background-color: #f5f7fb;
  overflow: hidden;
  border: none;
  outline: none;
  resize: none;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.button {
  border-radius: 20px;
  background-color: #0066cc;
  color: white;
  width: 120px;
  height: 40px;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #0656a7;
}
.button:active {
}
