.friendMessage {
  padding: 20px 0 20px 0;
}

.friendImageAndText {
  display: flex;
  gap: 10px;
  align-items: center;
}

.friendImageAndText img {
  border-radius: 50%;
  height: 50px;
}

.friendText {
  background-color: #f5f7fb;
  border-radius: 10px;
  padding: 8px;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.friendText:after {
  content: '';
  width: 0;
  height: 0;
  left: -10px;
  bottom: 0;
  border-bottom: 16px solid #f5f7fb;
  border-left: 16px solid transparent;
  position: absolute;
}

.friendName {
  margin-bottom: 5px;

  font-size: 16px;
  font-weight: 500;
}

.friendMessageText {
}

.friendTime {
  font-size: 10px;
  margin: 5px 0 0 12px;
}
