.message {
  padding: 20px 0 20px 0;
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse;
  gap: 10px;
  align-items: center;
}

.imageAndText img {
  border-radius: 50%;
  height: 50px;
}

.text {
  background-color: #0066cc;
  border-radius: 10px;
  color: #fff;
  padding: 8px;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.text:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: auto;
  right: -8px;
  bottom: 0;
  border-bottom: 16px solid #0066cc;
  border-right: 16px solid transparent;
}

.name {
  margin-bottom: 5px;
  text-align: end;
  font-size: 16px;
  font-weight: 500;
}

.messageText {
  font-size: 14px;
  text-align: end;
  font-weight: 300;
}

.time {
  display: flex;
  justify-content: end;
  font-size: 10px;
  margin: 5px 12px 0 0px;
}
