.App {
  --header_height: 60px;
  height: 100vh;
}

.hw {
  max-width: 1280px;
  width: 90%;
  margin: 50px auto;

  overflow: hidden;
}

.hwTitle {
  max-width: 1280px;
  width: 90%;
  margin: 0 auto;
  font-weight: 600;
  margin-top: 60px;
}

.App:before {
  content: '';
  display: inline-block;
  height: 1.5px;
  width: 100%;
  top: 90px;
  background-color: rgba(233, 231, 231, 0.633);
  position: absolute;
}
